

.Netflix{
    
    background: linear-gradient(0deg,rgba(0,0,0,.8) 0,transparent 60%,rgba(0,0,0,.8)), url('../Accects/banner.jpg') no-repeat;
    width: 100%;
    position: relative;
    &::after{
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        background: rgba(0,0,0,.4);
        height: 100%;
        width: 100%;
    }
    .content{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        @media (max-width:768px) {
           position: relative;
           transform: inherit;
           left: auto;
           top: 20%;
           margin-top: 40%;
        }
        .button-group{
            display: block;
            margin: auto;
            position: relative;
            max-width: 622px;
            @media (max-width:768px) {
                text-align: center;
                max-width: 100%;
                padding: 20px;
            }
            .form-control{
                height: 60px;
                width: 450px;
                padding: 10px;
                outline: none;
                color: #e50914;
                @media (max-width:768px) {
                    width: 100%;
                }
            }
            button{
                background: linear-gradient(to right, #f23637 0%, #fc0b68 100%);
                position: absolute;
                right: 0;
                top: 0;
                font-size: 1.625rem;
                min-height: 60px;
                padding: 0 1em;
                height: 100%;
                @media (max-width:768px) {
                    position: relative;
                    right: auto;
                    left: auto;
                    top: 30px;
                }
            }
        }
    }
}

.video-frame{
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: -1;
    &.position{
        top: 33%;
        video{
            width: 396px;
            max-width: 396px;
            @media (max-width:768px) {
                width: 100%;
                max-width: 100%;
            }
        }
    }
    video{
        width: 423px;
        max-width: 423px;
        height: auto;
        @media (max-width:768px) {
            width: 100%;
            max-width: 100%;
        }
    }
}
.accordian{
    width: 800px;
    background-color: rgb(48, 48, 48);
    @media (max-width:768px) {
       width: 100%;
    }
}